<template>
 <form @submit.prevent="handleSubmit">
  <div class="container">
    <h1>DAtepicker</h1>

    <div>
      <input type="checkbox" id="coding" v-model="hobbies" />
      <label for="coding">Games</label>
    </div>
    <div>
      <input type="checkbox" id="music" v-model="hobbies" />
      <label for="music">Music</label>
    </div>

    <!--short hand syntax @eventname="method" -->
    <!-- <button @click="handleClick"></button> -->

    <!-- long hand syntax -->
    <!-- <button v-on:click="handleClick"></button> -->

    <div>
      <select v-model="rating">
        <option disabled value>Choose your Rating</option>
        <option>1</option>
        <option>2</option>
        <option>3</option>
        <option>4</option>
        <option>5</option>
      </select>
    </div>
    <p>You've rated: {{ rating }}</p>
    <div>
      <input type="radio" id="male" value="Male" v-model="gender" />
      <label for="male">Male</label>
      <input type="radio" id="female" value="Female" v-model="gender" />
      <label for="female">Female</label>
      <p>Gender: {{ gender }}</p>
    </div>
      <!-- computed property -->
   <li>Full name : {{fullName}}</li>

   <!-- in Vue, watchers help us to watch every change in the data properties.
   it means if we assign a watcher to a particular data property
    it keeps track the every data change occurred to this property. -->

    <h1>{{ count }}</h1>
    <button @click="count += 1;">Increment</button>

<!-- .lazy: By default v-model directive updates the data property on every keypress
 by adding .lazy modifier it only updates the data after a change event is triggered.
 .trim: It is used to remove the leading and trailing white spaces.
 .number: If you want to accept number to the input field then you need to use .number modifier. -->
    <input v-model.lazy="name" placeholder="name"/>
     <input v-model.trim="name" placeholder="name"/>
      <input v-model.number="age" placeholder="age"/>
<!-- 
In the above code, we have added ref attribute to the my-counter component.
Inside the button element we can access the component by using $refs property and call the component method.
        <my-counter ref="c"></my-counter>
    <button @click="$refs.c.increment()">Increment</button> -->


  </div>
  </form>
  <!-- Here we added a @submit event listener to the form element with .prevent modifier,
   so that we stopped the browser default re-loading behavior whenever we submit a form. -->
</template>


<script>
//  import buttons from './components/buttons.vue';
export default {
  data: function () {
    return {
      hobbies: [],
      rating: "",
      gender: "",
      firstName: "Sai",
         lastName: "Gowtham", 
         count: 0
    };
  },
   methods:{
      handleSubmit:function(){
          console.log('Name'.this.name);
          console.log('Gender'.this.gender);
      }
  },
  computed:{
      fullName:function(){
          return this.firstName+' '+this.lastName
      }
  },
  watch: {
    //the function runs every time when a count data property changes
    count: function(value) {
      // we can access the count value in function parameter
      if (value === 5) {
        setTimeout(() => {
        // resetting the count value
          this.count = 0;
        }, 300);
      }
    }
   },
  //  components:{
  //       'my-button':buttons
  //   }
};
</script>

<style>
</style>



