<template>
  <div class="jumbotron">
    <div class="container">
      <div class="row">
        <div class="col-sm-8 offset-sm-2">
          <div>
            <form @submit.prevent="handleSubmit">
              <div class="form-group">
                <!-- <label for="Username">Username</label> -->
                <input
                  type="text"
                  v-model="user.uid"
                  id="uid"
                  name="uid"
                  readonly
                  class="form-control"
                />
              </div>
              <div class="form-group">
                <!-- <label for="Username">Username</label> -->
                <input
                  type="text"
                  v-model="user.userName"
                  id="username"
                  name="username"
                  placeholder="Enter Username"
                  class="form-control"
                  :class="{ 'is-invalid': submitted && $v.user.userName.$error }"
                />
                <div
                  v-if="submitted && !$v.user.userName.required"
                  class="invalid-feedback"
                >Username Name is required</div>
              </div>
              <div class="form-group">
                <!-- <label for="FullName">Full Name</label> -->
                <input
                  type="text"
                  v-model="user.fullName"
                  id="fullname"
                  name="fullname"
                  placeholder="Enter Fullname"
                  class="form-control"
                  :class="{ 'is-invalid': submitted && $v.user.fullName.$error }"
                />
                <div
                  v-if="submitted && !$v.user.fullName.required"
                  class="invalid-feedback"
                >Full Name is required</div>
              </div>
              <div class="form-group">
                <!-- <label for="email">Email</label> -->
                <input
                  type="email"
                  v-model="user.email"
                  id="email"
                  placeholder="Enter Email Id"
                  name="email"
                  class="form-control"
                  :class="{ 'is-invalid': submitted && $v.user.email.$error }"
                />
                <div v-if="submitted && $v.user.email.$error" class="invalid-feedback">
                  <span v-if="!$v.user.email.required">Email is required</span>
                  <span v-if="!$v.user.email.email">Email is invalid</span>
                </div>
              </div>

              <div class="form-group">
                <label style="float:left">Roles</label>
                <select class="form-control" v-model="user.roles">
                  <option value="1" selected>Admin</option>
                  <option value="2">User</option>
                </select>
                <div v-if="submitted && $v.user.roles.$error" class="invalid-feedback">
                  <span v-if="!$v.user.roles.required">Role is required</span>
                </div>
              </div>

              <div class="form-group">
                <label style="float:left" for="Created date">Created Date</label><br/>
                <div class="form-control">{{user.createdDate | formatDate}}</div>
                <!-- <input
                  type="text"
                  v-model="user.createdDate"
                  id="cdate"
                  name="cdate"
                  class="form-control"
                  :class="{ 'is-invalid': submitted && $v.user.createdDate.$error }"
                />
                <div
                  v-if="submitted && !$v.user.createdDate.required"
                  class="invalid-feedback"
                >Created date is required</div> -->
              </div>

              <div style="margin-top:30px" class="form-group">
                <button class="btn btn-primary">Save</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { required, email, minLength, sameAs } from "vuelidate/lib/validators";
import { formatDate } from "../filters";


export default {
  data() {
    return {
      //id:this.$route.params.id
      user: {
        uid: this.$route.params.uid,
        userName: "",
        fullName: "",
        email: "",
        password: "",
        confirmPassword: "123",
        roles: "",
        createdDate: "12/06/2020",
        activate: "1",
      },
      submitted: false,
    };
  },
   filters: {
    formatDate,
  },
  validations: {
    user: {
      userName: { required },
      fullName: { required },
      email: { required, email },
      roles: { required },
      createdDate: { required },
    },
  },

  methods: {
    handleSubmit(e) {
      this.submitted = true;
      // stop here if form is invalid
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }

      this.EditData();

      //alert("Sucessfully saved!! :-)\n\n" + JSON.stringify(this.user));
    },

    getData() {
      this.$http
        .get(this.$route.params.id)
        .then((response) => {
          //5alert(response.bodyText);
          return response.bodyText;
        })
        .then((data) => {
          this.user = JSON.parse(data);
        });
    },
    EditData() {
      this.$http.put("", this.user).then(
        (respone) => {
          if (respone.status == "200") {
            alert("Sucessfully saved!!'");
          } else {
            alert("Not saved!!'");
          }
          // alert(respone);
          //this.arr.push(this.model);
        },
        (error) => {
          alert(error);
        }
      );
    },
  },
  beforeMount() {
    this.getData();
  },
};
</script>

<style>
</style>


