<template>
  <div class="container" id="app">
    <h1> {{msg}} </h1>
   <hr>
    <app-header></app-header>
   <hr>
  <transition name="slide" mode="out-in">
     <router-view></router-view>
     </transition>
  </div>
</template>

<script>
import header from './components/header';
export default {
  name: 'app',
  data () {
    return {
      msg: 'MCN Vue.Js App'
    }
  },
  components:{
    appHeader:header
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
}
/*slide transition*/
.slide-enter-active,
.slide-leave-active
 {
  transition: transform 0.4s ease-out;
}
.slide-enter {
  transform: translateX(-30%);
}
.slide-leave-to {
  transform: translateX(30%);
}

</style>



