<template>
  <div class="jumbotron">
    <div style="float:left">
      <a href>Back To Home</a>
    </div>
    <div class="container">
      <table class="table">
        <th>Ids</th>
        <th>Username</th>
        <th>Email</th>
        <th>Role</th>
        <th>Status</th>
        <th>Delete | Edit</th>

        <tr v-for="item in Users" :key="item.Id">
          <td>{{item.uid}}</td>
          <td>{{item.userName}}</td>
          <td>{{item.email}}</td>


        <td v-if="item.roles=='2'" :class="{red:item.roles=='2',blue:item.roles=='1'}"> Admin </td>
        <td v-else  :class="{red:item.roles=='2',blue:item.roles=='1'}"> User</td>

        <td v-if="item.status=='0'"> Activated </td>
        <td v-else > Not activated</td>

          <!-- <td :class="{red:item.roles=='2',blue:item.roles=='1'}">{{item.roles}}</td> -->
          <td>
            <button @click="onDelete(item)" type="button" class="btn btn-default">
              <span class="glyphicon glyphicon-trash" aria-hidden="true"></span>
            </button>
            |
            <button @click="onEdit(item)" type="button" class="btn btn-default">
              <span class="glyphicon glyphicon-pencil" aria-hidden="true"></span>
            </button>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ID: 0,
      Username: "",
      Roles: 0,
      Email: "",
      Status: "",
      CreatedDate: "",
      Users: [
        // {
        //   UId: 1,
        //   Username: "Nandeesh",
        //   Email: "abc@g.com",
        //   Roles: "Admin",
        //   Status: "yes",
        // },
        // {
        //   UId: 2,
        //   Username: "Vinod",
        //   Email: "abc@g.com",
        //   Roles: "User",
        //   Status: "no",
        // },
        // {
        //   UId: 3,
        //   Username: "Ravi",
        //   Email: "abc@g.com",
        //   Roles: "Admin",
        //   Status: "yes",
        // },
      ],
    };
  },

  methods: {
    onDelete(item) {
      // this.Users.splice(this.Users.indexOf(item), 1);
      this.$http.delete(''+item.uid).then(respone=>{

        if(respone.status=="200")
         {
           alert("Sucessfully deleted!!'");
           this.Users.splice(this.Users.indexOf(item), 1);
         }
         else
         {
           alert("Not deleted!!'");
         }
      //alert(respone);
      //this.arr.splice(this.arr.indexOf(item),1);
    },
    error=>{
      alert(error);
    });

    },
    onEdit(item) {
      this.$router.push({ path: `/editaccount/${item.uid}` });
    },

    getData() {
      this.$http
        .get("")
        .then((response) => {
          //alert(response.bodyText);
          return response.bodyText;
        })
        .then((data) => {
          this.Users = JSON.parse(data);
        });
    },
  },
  beforeMount() {
    this.getData();
  },
};
</script>

<style>
.blue {
  color: blue;
}
.red {
  color: red;
}
</style>


