<template>
  <div class="jumbotron">
    <div class="container">
      <div class="row">
        <div class="col-sm-8 offset-sm-2">
          <div>
            <form @submit.prevent="handleSubmit">
              <div class="form-group">
                <!-- <label for="Username">Username</label> -->
                <input
                  type="text"
                  v-model="user.username"
                  id="Username"
                  name="username"
                  placeholder="Enter Username"
                  class="form-control"
                  :class="{ 'is-invalid': submitted && $v.user.username.$error }"
                />
                <div
                  v-if="submitted && !$v.user.username.required"
                  class="invalid-feedback"
                >Username Name is required</div>
              </div>
              <div class="form-group">
                <!-- <label for="FullName">Full Name</label> -->
                <input
                  type="text"
                  v-model="user.fullname"
                  id="fullname"
                  name="fullname"
                  placeholder="Enter Fullname"
                  class="form-control"
                  :class="{ 'is-invalid': submitted && $v.user.fullname.$error }"
                />
                <div
                  v-if="submitted && !$v.user.fullname.required"
                  class="invalid-feedback"
                >Full Name is required</div>
              </div>
              <div class="form-group">
                <!-- <label for="email">Email</label> -->
                <input
                  type="email"
                  v-model="user.email"
                  id="email"
                  placeholder="Enter Email Id"
                  name="email"
                  class="form-control"
                  :class="{ 'is-invalid': submitted && $v.user.email.$error }"
                />
                <div v-if="submitted && $v.user.email.$error" class="invalid-feedback">
                  <span v-if="!$v.user.email.required">Email is required</span>
                  <span v-if="!$v.user.email.email">Email is invalid</span>
                </div>
              </div>
              <div class="form-group">
                <!-- <label for="password">Password</label> -->
                <input
                  type="password"
                  v-model="user.password"
                  id="password"
                  name="password"
                  placeholder="Enter Password"
                  class="form-control"
                  :class="{ 'is-invalid': submitted && $v.user.password.$error }"
                />
                <div v-if="submitted && $v.user.password.$error" class="invalid-feedback">
                  <span v-if="!$v.user.password.required">Password is required</span>
                  <span v-if="!$v.user.password.minLength">Password must be at least 6 characters</span>
                </div>
              </div>
              <div class="form-group">
                <!-- <label for="confirmPassword">Confirm Password</label> -->
                <input
                  type="password"
                  v-model="user.confirmPassword"
                  id="confirmPassword"
                  name="confirmPassword"
                  placeholder="Enter Confirm password"
                  class="form-control"
                  :class="{ 'is-invalid': submitted && $v.user.confirmPassword.$error }"
                />
                <div v-if="submitted && $v.user.confirmPassword.$error" class="invalid-feedback">
                  <span v-if="!$v.user.confirmPassword.required">Confirm Password is required</span>
                  <span v-else-if="!$v.user.confirmPassword.sameAsPassword">Passwords must match</span>
                </div>
              </div>
              <div class="form-group">
                <label style="float:left">Roles</label>
                <select
                  class="form-control"
                  v-model="user.roles"
                  :class="{'is-invalid': validationStatus($v.user.roles)}"
                >
                  <option value="1" selected>Admin</option>
                  <option value="2">User</option>
                </select>
                <div v-if="submitted && $v.user.roles.$error" class="invalid-feedback">
                  <span v-if="!$v.user.roles.required">Role is required</span>
                </div>
              </div>
              <form class="form-inline">
                <div class="form-group mb-2">
                  <label
                    style="float:left;Margin-top:10px;margin-right:10px;"
                    for="Created date :"
                  >Created Date</label>
                  <datepicker
                    v-model="user.cdate"
                    id="cdate"
                    name="cdate"
                    class="input-group date"
                    :class="{ 'is-invalid': submitted && $v.user.cdate.$error }"
                  ></datepicker>

                  <div
                    v-if="submitted && !$v.user.cdate.required"
                    class="invalid-feedback"
                  >Created date is required</div>
                </div>

                <div class="form-group mx-sm-3 mb-2">
                  <input
                    type="checkbox"
                    v-model="user.activate"
                    id="activate"
                    name="activate"
                    class="form-check-input"
                    :class="{ 'is-invalid': submitted && $v.user.activate.$error }"
                  />
                  <label class="form-check-label" for="activate">Activate</label>

                  <div v-if="submitted && $v.user.activate.$error" class="invalid-feedback">
                    <span v-if="!$v.user.activate.required">Activate is required</span>
                  </div>
                </div>
              </form>
             
              <div style="margin-top:30px" class="form-group">
                <button class="btn btn-primary">Create</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { required, email, minLength, sameAs } from "vuelidate/lib/validators";
import Datepicker from "vuejs-datepicker";

export default {
  name: "app",
  data() {
    return {
      user: {
        username: "",
        fullname: "",
        email: "",
        password: "",
        confirmPassword: "",
        roles: "1",
        cdate: "",
        activate: "",
      },
      submitted: false,
    };
  },
  components: {
    Datepicker,
  },

  validations: {
    user: {
      username: { required },
      fullname: { required },
      email: { required, email },
      password: { required, minLength: minLength(6) },
      confirmPassword: { required, sameAsPassword: sameAs("password") },
      roles: { required },
      cdate: { required },
      activate: { required },
    },
  },
  methods: {
    handleSubmit(e) {
      this.submitted = true;
      // stop here if form is invalid
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }

      this.addData();

      //alert("Sucessfully created!! :-)\n\n" + JSON.stringify(this.user));
    },
    addData() {
      this.$http.post("", this.user).then(
        (respone) => {
          if (respone.status == "200") {
            alert("Sucessfully created!!'");
          } else {
            alert("Not created!!'");
          }
        },
        (error) => {
          alert(error);
        }
      );
    },
    validationStatus: function (validation) {
      return typeof validation != "undefined" ? validation.$error : false;
    },
  },
};
</script>



